import "./App.css";
import React, {useEffect, useState} from "react";
import { Button, Box, Stack} from "@mui/material";
import * as XLSX from 'xlsx';
import axios from 'axios'
import { CircularProgress,Backdrop } from "@mui/material";

const App = () => {
  const [loading, setLoading] = useState(false);

  const [selectedFile, setSelectedFile] = useState(null);
  const [tableData, setTableData] = useState([])
  const [search ,setSearch] = useState({})
  const[searchData, setSearchdata] = useState(null)

  useEffect(() => {
    // function renderfunction(){
      const itemsFromStorage = localStorage.getItem("ipCountry");
      if (itemsFromStorage !== null){
        setTableData(JSON.parse(itemsFromStorage));
      }
    // }
  }, []);
  
  // renderfunction()
 
  // const dummy = [{ip : "192.168.1.25",country : "India"},{ip : "192.168.1.2",country : "AUS"},{ip : "192.168.1.91",country : "China"},{ip : "192.168.1.80",country : "Uk"}]
  // const [tableData , setTableData] = useState([])
  
  const handleFileChange = (e) => {
    setSelectedFile(e.target.files[0]);
  };
    
  JSON.parse(localStorage.getItem("ipCountry"));

  const handleFileUpload = async () => {
    if (selectedFile === null) {
      // File is not selected, so don't proceed with the upload
      alert("file has no selected")
      console.log('No file selected for upload.');
      return
    }
    const formData = new FormData();
    formData.append('excelFile', selectedFile);
   try {
    setLoading(true); 
      let result = await axios.post('http://localhost:5000/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        }
      })
      console.log("hello",result);
      // setTableData( result.data )
      localStorage.setItem("ipCountry",JSON.stringify(result.data))
      const itemsFromStorage = localStorage.getItem("ipCountry");
      if (itemsFromStorage) {
        setTableData(JSON.parse(itemsFromStorage));
      }
      setLoading(false);
      // setTableData(JSON.parse(localStorage.getItem("ipCountry")))
      // setTableData(result.data)
    } catch (error) {
      console.error('Fetch error:', error);
      setLoading(false);
    }
  };
  const handleSearch = () => {
     let ip_data = JSON.parse(localStorage.getItem("ipCountry"))
     if(ip_data){
      for (const ipMatch of ip_data){
          if (ipMatch['ip'] == search['ip']){
          setSearchdata([ipMatch])
          break
          }else if (ipMatch['ip'] !== search['ip']){
          setSearchdata(null)
          }
       }
     }
  }
  const generateExcelFile = () => {
    const data  = JSON.parse(localStorage.getItem("ipCountry"))
    if(data == null){
      alert("Data is emtpy")
      return
    }   
    const ws = XLSX.utils.json_to_sheet(data);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
  
    // Generate a file name
    const fileName = 'MARS.xlsx';
  
    // Generate a Blob object containing the Excel file
    XLSX.writeFile(wb, fileName);
  };
  
  return (
    <Box>
      <Stack
        direction="row"
        justifyContent="center"
        alignItems="flex-end"
        spacing={2}
      >
        <Box>
          <input  type="file" onChange={handleFileChange} className="file" />
          {/* <button type="button" style={{borderRadius : "20px",height : "30px",width : "90px"}} >Upload</button> */}
          <Button
            color="success"
            variant="contained"
            size="small"
            sx={{ borderRadius: "15px" }}
            onClick={handleFileUpload}
          >
            UploadS
          </Button>
        </Box>
      </Stack>
      {/* <Box
        sx={{
          display: "flex",
          justifyContent: "end",
          marginRight: "10px",
        }}
      >
        <Button
          sx={{ border: "2px solid", marginLeft: "50px" }}
          color="success"
          variant="contained"
          size="small"
        >
          Export
        </Button>
      </Box> */}

      <Box
        sx={{
          display: "flex",
          flexFlow : "column",
          gap : "10px",
          alignItems : "center",
          justifyContent: "center",
          marginTop: "20px",
        }}
      >
      <div>
        <input type="search" id="search" name="seach" placeholder="Search for something..." onChange={(e)=>setSearch({ip : e.target.value})} />
        <input type="button" id="submit" value= 'Search' onClick={handleSearch}/>
      </div>
      <div style={{ width: 800, position : "relative"}}><Button onClick={generateExcelFile} variant="contained" color="success" sx = {{marginRight : "10px", position:"absolute", right : "0"}}>Export</Button></div>
      <div class="tableFixHead" >
      <table>
        <thead style={{position:"sticky"}}>
          <tr>
            <th>IP</th>
            <th>Country</th>
            <th>AS Name</th>
            <th>Organization Name</th>
            <th>Active Domain</th>
            <th>Attack Desciption</th>
            <th>Attack Category</th>
            <th>Risk</th>
            <th>Cloud Name</th>
          </tr>
        </thead>
        <tbody>{loading ? (
  <Backdrop open={loading} style={{ zIndex: 9999, color: "#fff" }}>
    <CircularProgress color="inherit" />
  </Backdrop>
) : ( 
  searchData == null ? ( tableData !== null ? ( tableData.map((element)=>{
    return <tr><td>{element.ip}</td><td>{element.country}</td><td>{element.AS_NUM}</td><td>{element.ORG_NAME}</td><td></td><td>{element.Attack_Description}</td></tr>
        })):( <tr><td></td><td></td><td></td><td></td></tr>)) :( searchData.map((element)=>{
          return <tr><td>{element.ip}</td><td>{element.country}</td><td>{element.AS_NUM}</td><td>{element.ORG_NAME}</td><td></td><td>{element.Attack_Description}</td></tr>
              }))
 )}
        </tbody>
      </table>
    </div>
      </Box>
    </Box>
  );
};
export default App;

